import BASE from './../../utils/base'
const changeDp = () => {
	let db = BASE.dragPosrect();
	db.height = 'auto';
	return db
};
export default {
	getData () {
		return {
			id: '',
			label: '',
			text: '',
			enableDrag: true,
			resize: 'w', //wh, all 
			style:{
				dragPosrect: changeDp(),
				ft: BASE.ft()
			}
		}
	}
}


