import BASE from './../../utils/base'
const changeFt = () => {
	let ft = BASE.ft();
	ft.fontWeight = 700;
	return ft;
};
export default {
	getData () {
		return {
			$$key: '',
			id: '',
			label: '',
			enableDrag: false,
			text: '',
			style:{
				opacity: 1,
				paddingTop: 0,
				paddingBottom: 0,
				ft: changeFt()
			}
		}
	}
}


