<template>
	<div class="banner-panel">
		<el-tabs v-model="acitveTab">
			<el-tab-pane label="&" name="style" class="editor-pane">
				<div class="el-form-item">
					<label class="el-form-item__label" style="width:60px">ID</label>
					<div class="el-form-item__content" style="margin-left:60px;">
						<el-input size="small" class="br2" v-model="data.id" placeholder="htmlID"></el-input>
					</div>
				</div>
				<div class="el-form-item">
					<label class="el-form-item__label" style="width:60px"></label>
					<div class="el-form-item__content" style="margin-left:60px;">
						<el-input size="small" class="br2" v-model="data.label" placeholder=""></el-input>
					</div>
				</div>
				<div class="el-form-item">
					<label>Banner</label>
					<div class="el-form-item__content">
						<el-input size="small" class="br2" v-model="data.bannerImg" placeholder=""></el-input>
					</div>
				</div>
				<div class="tab-line">
					<div class="tab-line--title"></div>
				</div>
				<div class="el-form-item">
					<label class="el-form-item__label" style="width:50px;"></label>
					<div class="el-form-item__content" style="margin-left:50px;">
						<c-input-number size="small" v-model="data.style.opacity" :step="0.1" :min="0" :max="1"></c-input-number>
					</div>
				</div>
				<div class="el-form-item">
					<label class="el-form-item__label" style="width:50px;">H</label>
					<div class="el-form-item__conetnt" style="margin-left:50px;">
						<el-input size="small" v-model="data.style.height" class="br2"></el-input>
					</div>
				</div>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>
<script>
	import CInputNumber from './../../../editor_components/input_num/input_num'
	export default {
		name: 'bannerEditor',
		components:{
			CInputNumber
		},
		props:{
			data: {
				type: Object
			}
		},
		computed:{
			bannerImg () {
				return this.data.bannerImg;
			}
		},
		watch:{
			bannerImg:{
				immediate: true,
				handler (nv, ol) {
					const _this = this;
					// _this.$nextTick(() => {
						let img = new Image();
						img.onload = function(){
							img.onload = null;
							let h = img.height;
							_this.data.style.height = h;
						};
						img.src = nv;
					// });
				}
			}
		},
		data () {
			return {
				acitveTab: 'style',
				posType: 'static'
			}
		}
	}
</script>
<style lang="scss" scoped>
	.banner-panel{
		width: 100%;
		background-color: #D3DCE6;
		box-sizing: border-box;
	}
	.tag-item{
		display: inline-block;
		height: 100%;
		padding: 0 8px;
	}
</style>


